import { IconType } from './icon';

//
// Item
//

export class RespTextItem {
  icon: IconType;
  text: string;

  private constructor(icon: IconType = null, text: string = null) {
    this.icon = icon;
    this.text = text;
  }

  static createIcon(icon: IconType): RespTextItem {
    return new RespTextItem(icon, null);
  }

  static createText(text: string): RespTextItem {
    return new RespTextItem(null, text);
  }

  static createIconAndText(icon: IconType, text: string): RespTextItem {
    return new RespTextItem(icon, text);
  }
}

//
// Item type
//

export type RespTextItemType = string | RespTextItem;

//
// Text
//

export class RespText {
  text: RespTextItemType;
  smallText: RespTextItemType;

  private constructor(text: RespTextItemType = null, smallText: RespTextItemType = null) {
    this.text = text;
    this.smallText = smallText;
  }

  static create(text: RespTextItemType, smallText: RespTextItemType): RespText {
    return new RespText(text, smallText);
  }
}

//
// Text type
//

export type RespTextType = string | RespTextItem | RespText;
