import { Component, OnInit, Input } from '@angular/core';

import {
  RespTextItemType,
  RespTextItem,
} from '../../models/components/resp-text-model';
import { IconType, Icon } from '../../models/components/icon';
import { RespTextCheckerService } from '../../services/resp-text-checker.service';
import { IconCheckerService } from '../../services/icon-checker.service';

enum IconProperty {
  Icon = 'Icon',
  Scale = 'Scale',
}

@Component({
  selector: 'app-resp-text-item',
  templateUrl: './resp-text-item.component.html',
  styleUrls: ['./resp-text-item.component.scss']
})
export class RespTextItemComponent implements OnInit {
  @Input() textItem: RespTextItemType;

  get textItem_icon(): IconType {
    return this.checker.isItem(this.textItem) ? (this.textItem as RespTextItem).icon : null;
  }

  get textItem_text(): string {
    return this.checker.isItem(this.textItem) ? (this.textItem as RespTextItem).text : null;
  }

  get iconClass(): string {
    return this.getIconPropertyValue(IconProperty.Icon);
  }

  get iconScale(): number {
    return this.getIconPropertyValue(IconProperty.Scale);
  }

  constructor(
    public checker: RespTextCheckerService,
    private iconChecker: IconCheckerService,
  ) { }

  ngOnInit() {
  }

  private getIconPropertyValue(iconProperty: IconProperty): any {
    let propertyValue: any = null;

    if (this.checker.isItem(this.textItem)) {
      const typedTextItem = this.textItem as RespTextItem;

      if (this.iconChecker.isString(typedTextItem.icon)) {

        if (this.iconPropertyIsIcon(iconProperty)) {
          propertyValue = typedTextItem.icon;
        } else if (this.iconPropertyIsScale(iconProperty)) {
          propertyValue = 1;
        }

      } else if (this.iconChecker.isIcon(typedTextItem.icon)) {
        const typedIcon = typedTextItem.icon as Icon;

        if (this.iconPropertyIsIcon(iconProperty)) {
          propertyValue = typedIcon.icon;
        } else if (this.iconPropertyIsScale(iconProperty)) {
          propertyValue = typedIcon.scale || 1;
        }

      }
    }

    return propertyValue;
  }

  private iconPropertyIsIcon(iconProperty: IconProperty): boolean {
    return iconProperty === IconProperty.Icon;
  }

  private iconPropertyIsScale(iconProperty: IconProperty): boolean {
    return iconProperty === IconProperty.Scale;
  }

}
