import { NavbarLink, NavbarLinkActiveOptions } from './navbar-link-group-model';
import {
  RespTextType,
  RespText,
  RespTextItem,
} from './resp-text-model';
import { Icon } from './icon';

export class GitHubLink implements NavbarLink {

  private static readonly icon = 'fab fa-github';

  text: RespTextType;
  url: string;
  routerLinkActiveOptions?: NavbarLinkActiveOptions;
  external?: boolean;

  constructor(url: string) {
    this.text = RespText.create(
      RespTextItem.createIconAndText(
        Icon.create(GitHubLink.icon, 1.5),
        'GitHub',
      ),
      RespTextItem.createIcon(
        Icon.create(GitHubLink.icon, 1.8),
      ),
    );
    this.url = url;
    this.routerLinkActiveOptions = null;
    this.external = true;
  }
}