import {
  Component,
  OnInit,
  Input,
  ViewChild,
  ElementRef,
} from '@angular/core';

import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss']
})
export class ModalComponent implements OnInit {
  @ViewChild('content', null) content: ElementRef;

  @Input() title: string | string[];

  get titleAsHtml(): string {
    let result: string;

    if (!this.title) {
      result = '';
    } else if (typeof this.title === 'string') {
      result = this.title;
    } else {
      result = (this.title as string[]).join('<br>');
    }

    return result;
  }

  constructor(private modalService: NgbModal) { }

  ngOnInit() {
  }

  open() {
    this.modalService.open(this.content, { scrollable: true, size: 'lg' }).result.then(
      (result) => {
        // OK
      },
      (reason) => {
        // Cancel
      },
    );
  }

  onClickDonateUs(): void {
    window.location.hash = '#donate-us';
    document.getElementById('id-donate-us').scrollIntoView();
  }

}
