import { Injectable } from '@angular/core';

import { IconType, Icon } from '../models/components/icon';

@Injectable({
  providedIn: 'root'
})
export class IconCheckerService {

  constructor() { }

  isString(icon: IconType): boolean {
    return typeof icon === 'string';
  }

  isIcon(icon: IconType): boolean {
    return icon instanceof Icon;
  }
}
