import { Injectable } from '@angular/core';

import {
  RespTextType,
  RespText,
  RespTextItem,
} from '../models/components/resp-text-model';

@Injectable({
  providedIn: 'root'
})
export class RespTextCheckerService {

  constructor() { }

  isString(text: RespTextType): boolean {
    return typeof text === 'string';
  }

  isItem(text: RespTextType): boolean {
    return text instanceof RespTextItem;
  }

  isText(text: RespTextType): boolean {
    return text instanceof RespText;
  }
}
