import {
  Component,
  AfterViewInit,
  ViewChild,
} from '@angular/core';

import 'prismjs';
import 'prismjs/components/prism-css';
import 'prismjs/components/prism-scss';

import { NavbarLinkGroup, NavbarLink } from './shared/models/components/navbar-link-group-model';
import { GitHubLink } from './shared/models/components/github-link';
import { ModalComponent } from './shared/components/modal/modal.component';
import { userRelativeUrls } from './user/user-routing.module';
import { environment } from '../environments/environment';

const fcssNavbarLinkGroup: NavbarLinkGroup = {
  title: 'Features CSS',
  links: [
    {
      text: 'About',
      url: userRelativeUrls.aboutFeaturesCss,
    },
    {
      text: 'Team',
      url: userRelativeUrls.featuresCssTeam,
    },
    new GitHubLink('https://github.com/features-css/features-css'),
  ],
};

const saveProjectLinkGroup: NavbarLinkGroup = {
  title: 'Save Project',
  links: [
    {
      text: 'About',
      url: userRelativeUrls.aboutSaveProject,
    },
    {
      text: 'Team',
      url: userRelativeUrls.saveTeam,
    },
    new GitHubLink('https://github.com/save-team'),
  ],
};

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements AfterViewInit {
  @ViewChild('pleaseHelpMeModal', null) pleaseHelpMeModal: ModalComponent;

  shareUrl = `${environment.appUrlNoEndsWithSlash}/about-features-css`;
  shareTitle = 'Hi! Take a look at the Features CSS. This is the next generation CSS-Preprocessor...';
  shareTags = ['features-css'].join(' ');

  navbarLinkGroups: NavbarLinkGroup[] = [];

  ngAfterViewInit(): void {
    let pleaseHelpMeShowedOnce = localStorage.getItem('pleaseHelpMeShowedOnce');
    pleaseHelpMeShowedOnce = 'true';
    if (!pleaseHelpMeShowedOnce) {
      this.openPleaseHelpMeModal();
      localStorage.setItem('pleaseHelpMeShowedOnce', 'true');
    }
  }

  onClickDonateMeAndMyAnimals(e: Event): void {
    e.preventDefault();
    this.pleaseHelpMeModal.open();
  }

  private openPleaseHelpMeModal(): void {
    this.pleaseHelpMeModal.open();
  }
}
