import { Component, OnInit, Input } from '@angular/core';

import {
  RespTextType,
  RespText,
  RespTextItemType,
  RespTextItem,
} from '../../models/components/resp-text-model';
import { RespTextCheckerService } from '../../services/resp-text-checker.service';

@Component({
  selector: 'app-resp-text',
  templateUrl: './resp-text.component.html',
  styleUrls: ['./resp-text.component.scss']
})
export class RespTextComponent implements OnInit {
  @Input() text: RespTextType;

  get text_text(): RespTextItemType {
    return this.checker.isText(this.text) ? (this.text as RespText).text : null;
  }

  get text_smallText(): RespTextItemType {
    return this.checker.isText(this.text) ? (this.text as RespText).smallText : null;
  }

  constructor(
    public checker: RespTextCheckerService,
  ) {}

  ngOnInit() {
  }

}
