import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AboutFeaturesCssContainerComponent } from './containers/about-features-css-container/about-features-css-container.component';
import { FeaturesCssTeamContainerComponent } from './containers/features-css-team-container/features-css-team-container.component';
import { AboutSaveProjectContainerComponent } from './containers/about-save-project-container/about-save-project-container.component';
import { SaveTeamContainerComponent } from './containers/save-team-container/save-team-container.component';

import {
  ModuleUrls,
  ModuleUrlsItem,
  ModuleUrlsRelativeContext,
} from '../shared/logic/module-urls';

//
// Urls
//

export const userRelativeUrls = {
  aboutFeaturesCss: 'about-features-css',
  featuresCssTeam: 'features-css-team',
  aboutSaveProject: 'about-save-project',
  saveTeam: 'save-team',
};

//
// Routes
//

const routes: Routes = [
  {
    path: '',
    component: AboutFeaturesCssContainerComponent,
  },
  {
    path: userRelativeUrls.aboutFeaturesCss,
    component: AboutFeaturesCssContainerComponent,
  },
  {
    path: userRelativeUrls.featuresCssTeam,
    component: FeaturesCssTeamContainerComponent,
  },
  {
    path: userRelativeUrls.aboutSaveProject,
    component: AboutSaveProjectContainerComponent,
  },
  {
    path: userRelativeUrls.saveTeam,
    component: SaveTeamContainerComponent,
  },
  {
    path: '**',
    redirectTo: '',
  },
];

//
// Module
//

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserRoutingModule { }
